@import url(https://fonts.googleapis.com/css?family=Orbitron);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

@keyframes infiniteScrollBg{0%{background-position:50% 0}100%{background-position:50% -1000px}}div{display:flex;flex-direction:column;align-items:center;text-align:center;padding:20px}body{display:flex;font-family:Orbitron;justify-content:space-around;align-items:center;text-align:center;margin:0;padding:0;width:100%;background-image:url("https://monophy.com/media/YWzwa1nGjXk2xufkVM/monophy.gif");animation:infiniteScrollBg 70s linear infinite;display:flex;flex-direction:column}.navbar{display:flex;flex-wrap:wrap;justify-content:space-between;width:100%;margin-top:20px;max-width:100%;padding:50px;margin:0}.title{color:#3b0006;text-align:center;margin-top:25px;font-size:40px}.navbar-button{max-width:100;height:40px;color:#fff;font-weight:bold;cursor:pointer;transition:all 0.3s ease;position:relative;display:inline-block;outline:none;border:2px solid #6a040f;background:#6a040f}.navbar-button:hover{background:#fff;color:#6a040f}

.digital-clock{display:flex;justify-content:center;align-items:center;text-align:center;max-width:100%;color:#49030a;background-color:#ffffff91;border:solid 6px #6a040f;border-radius:20px;padding:50px 80px;font-size:20px;margin-top:100px}

.stopwatch{display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center;max-width:100%;color:#49030a;background-color:#ffffff91;border:solid 8px #6a040f;border-radius:20px;padding:50px 80px;margin-top:80px;font-size:20px}.stopwatch .buttons{cursor:pointer;width:150px;max-width:100%;height:25px;margin:7px;border-radius:15px;background-color:#49030a;color:white;border:solid 1px #6a040f}

.countdown{display:flex;justify-content:center;align-items:center;text-align:center;max-width:100%;color:#49030a;background-color:#ffffff91;border:solid 8px #6a040f;border-radius:20px;padding:50px 80px;margin-top:80px;font-size:20px}

